<template>
  <div class="addlist">
      <van-nav-bar title="地址列表" left-text="返回" left-arrow @click-left="back">
        </van-nav-bar>
        <div class="bj"></div>
       <div class="yj">
    <van-address-list
      :list="list"
      default-tag-text="默认"
      :switchable="false"
      @add="add"
      @edit="edit"
    />
    </div> 
  </div>
</template>

<script>
export default {
  name: "Addlist",
  data() {
    return {
      list: [],
    };
  },

  created(){
      this.getlist();
  },

  methods: {

    back(){
        this.$router.go(-1);
    },
    //新增
    add(){
        this.$router.push({name:'Addedit'})
    },

    //编辑
    edit({aid}){
        this.$router.push({name:'Addedit' , params:{aid}})
    },

    //查询用户地址列表
    getlist(){
    let tokenString = localStorage.getItem('tokenString');
      

      if(!tokenString ){
        return this.$router.push({name:"Login"});
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "GET",
        url: this.baseUrl + "/findAddress",
          params: {
          appkey: this.appkey,
          tokenString
          },
      }).then((res) => {
          
          this.$toast.clear();

          if(res.data.code === 700){
          this.$toast({
            message:res.data.msg,
            forbidClick: true,
            duration: 3000
          })
          return this.$router.push({name:'Login'});
        }

          if (res.data.code === 20000) {
                res.data.result.forEach(v =>{
                    v.address = `${v.province}${v.city}${v.county}${v.addressDetail}`
                    v.isDefault = Boolean(v.isDefault)
                })
                // 
                this.list = res.data.result; 
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>

<style lang="less" scoped>
.addlist{
    .bj{
        height: 120px;
        background: #f5bd00;
    }
    .yj{
        position: relative;
        top:-20px;
        width: 100%;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
    }
    /deep/ .van-tag--danger{
        background: #f5bd00;
    }
    /deep/ .van-button--danger{
        background: #f5bd00;
        border: 0.02667rem solid #f5bd00;
    }
}
</style>